<template>
    <el-tree
        :data="data"
        node-key="id"
        @node-click="handleNodeClick"
        :default-expanded-keys="['1-1', '1-2']"
        :default-checked-keys="['1-1']"
        >
    </el-tree>
</template>

<script>
import { getTimestamp, getTimeStamp } from '../../utils/StringUtils.js';
export default {
   name: 'blog-tree',
   data(){
       return {
           data:null
       }
   },
   watch:{
       $route:function(old,last){
           this.getData();
       }
   },
   created(){
       this.getData();
   },
   methods:{
       getData(){
           let hash = window.location.hash.replace('#','').trim();
           let version = getTimeStamp();
           let _this =this;
           if(this.data !=null)
             this.data.splice(0,this.data.length);
           fetch('db/tree/navtree.json?v='+version)
                .then(function(response) {
                    return response.json();
                })
                .then(function(data) {
                    data.filter(item=>{
                        if(item.chapter==hash){
                            _this.data=item.children;
                            return true;
                        }
                    });
                });
          
       },
       handleNodeClick(data){
           let hash = window.location.hash.replace('#','').trim();
           let obj={
               category:hash,
               indentity:data.id,
               label:data.label
           }
           this.$store.commit("changePageObj",obj);
       }
   }
}
</script>

